import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import * as moment from 'moment';
import { TestScheduleService } from '../../service/service/testing/test-schedule.service';

@Component({
  selector: 'app-view-details-dialog',
  templateUrl: './view-details-dialog.component.html',
  styleUrls: ['./view-details-dialog.component.css']
})
export class ViewDetailsDialogComponent implements OnInit {

  constructor(@Inject(MAT_DIALOG_DATA) public datas: any,
    public api: TestScheduleService,
    public dialogRef: MatDialogRef<ViewDetailsDialogComponent>) { }

  
  subjects: object[] = [];
  checkSubject: any = [];
  testID:number = 0;
  TestTypeName:string = "";
  TestDateString:string = "";
  Description:string = "";
  Location:string = "";
  RegularRegistrationDate:any = new Date();
  LateRegistrationDate:any = new Date();
  RegDateIsModifiable:boolean = false;
  IsRegistered:boolean = true;

  ngOnInit() {
    this.initData();
  }

  //数据初始化
  initData(){

    console.log(this.datas);
    const test = this.datas.test;
    const subjects = this.datas.subjects;

    this.testID = test.TestID; 
    this.TestTypeName = test.TestTypeName;
    this.TestDateString = test.TestDateString;
    this.Description = test.Description;
    this.Location = test.Location;
    this.RegDateIsModifiable = test.RegDateIsModifiable;
    this.IsRegistered = test.IsRegistered;
    this.RegularRegistrationDate = this.limitDate(test.RegularRegistrationDate);
    this.LateRegistrationDate = this.limitDate(test.LateRegistrationDate);
    this.subjects = subjects;
    for (let item of test.SubjectList)
      this.checkSubject.push(item.ID);
    
  }

  //截取时间
  limitDate(date) {
    if (date == null || date == "")
      return null;
    return date.substr(0, 10);
  }

  cancle() {
    this.dialogRef.close();
  }

  // 比较两个时间的大小
  checkTime(date1, date2) {
    date1 = date1.replace(/-/g, "/");
    date2 = date2.replace(/-/g, "/");
    var time1 = new Date(date1).getTime();
    var time2 = new Date(date2).getTime();
    if (time1 > time2) {
      return 1;
    } else if (time1 == time2) {
      return 2;
    } else {
      return 3;
    }
  }

  save() {
    if (this.checkSubject.length > 3) {
      alert('Only 2 subjects may be entered for this type of test!');
      return;
    }

    const obj = {
      testID: this.testID,
      location: this.Location,
      regDateString: moment(this.RegularRegistrationDate).format('YYYY-M-D').indexOf('-') < 0 ? '' : moment(this.RegularRegistrationDate).format('YYYY-M-D'),
      lateRegDateString: moment(this.LateRegistrationDate).format('YYYY-M-D').indexOf('-') < 0 ? '' : moment(this.LateRegistrationDate).format('YYYY-M-D'),
      isRegistered: this.IsRegistered,
      subjectIDs: this.checkSubject
      // regDateIsModifiable: true,
    }

    // regDateString必须在TestDate之前， lateRegDateString必须在TestDate之后

    if (this.checkTime(this.datas.test.TestDate, obj.regDateString) === 3) {
      alert('The registration date must be prior to the test date.');
      return;
    }

    if (this.checkTime(this.datas.test.TestDate, obj.lateRegDateString) === 1) {
      alert('Late registration date must be after the registration date.');
      return;
    }

    if (obj.lateRegDateString == '') {
      alert('Late registration date null.');
      return;
    }

    console.log(obj);
    this.api.POST(this.api.configPath.SaveTest, obj, true).then((res) => {
      this.dialogRef.close(res);
    });

  }

}
