import { Injectable } from '@angular/core';
import { TestingFactoryService } from '../testing-factory.service';
import { HttpClient } from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class TestsDashboardService extends TestingFactoryService {

  constructor(protected http: HttpClient) {
    super(http);
  }

  // get Score Comparison
  public api_get_ScoreComparison() {
    return new Promise<any>(resolve => {
      this.get('/testing/GetACTSATCompareModel').then(res => {
        resolve(res);
      });
    });
  }
  // get /testing/GetDashboardViewModel
  public api_get_DashboardView() {
    return new Promise<any>(resolve => {
      this.get('/testing/GetDashboardViewModel').then(res => {
        resolve(res);
      });
    });
  }

  // delete
  public api_delete_DeleteTests(id) {
    return new Promise<any>(resolve => {
      this.get('/testing/DeleteTests?TestID=' + id).then(res => {
        resolve(res);
      });
    });
  }

  // get subjects
  public api_get_TestSubjects(testId) {
    return new Promise<any>(resolve => {
      this.get('/testing/GetTestSubjects?testId=' + testId).then(res => {
        resolve(res);
      });
    });
  }
  // updateTest
  public api_post_SaveTest(params) {
    return new Promise<any>(resolve => {
      this.post('/Testing/SaveTest', params).then(res => {
        resolve(res);
      });
    });
  }

}
