import { Component, OnInit } from '@angular/core';
import { CalendarView,CalendarEvent} from 'angular-calendar';
import { Chart  } from 'angular-highcharts';
import { MatDialog } from '@angular/material';
import { TestsDashboardService } from '../../service/service/testing/tests-dashboard.service';
import { ViewDetailsDialogMytestComponent } from '../view-details-dialog-mytest/view-details-dialog-mytest.component';
import { MyTestsService } from '../../service/service/testing/my-tests.service';
import { LoadingService } from './../../shared/loading/service/loading.service';
const colors: any = {
  lateRegDate: {
    primary: '#f8948d',
    secondary: '#FAE3E3'
  },
  regDate: {
    primary: '#6fd59a',
    secondary: '#D1E8FF'
  },
  testDate: {
    primary: '#f9b635',
    secondary: '#FDF1BA'
  },
  today: {
    primary: '#fff04d',
    secondary: '#fff04d'
  }
}; // 日历颜色
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})


export class DashboardComponent implements OnInit {
  bestScoresList: any; // score Comparision列表
  satData: any;
  actData: any;
  chartSAT = new Chart(); // SAT highcharts图
  chartACT = new Chart(); // ACT highcharts图
  chartJudge = false; // highcharts judge
  events: CalendarEvent[]; // 日历数据
  viewDate: Date = new Date();
  view: CalendarView = CalendarView.Month;
  upcomingTestsData: any = null;
  upcomingTestsColumns: string[] = [
      'TestDateAsString',
      'TestType',
      'SubjectNames',
      'registered',
      'RegistrationDateAsString',
      'LateRegistrationDateAsString',
      'point']; // upcomingTest表格头部
  calendarEventArr: any = [
    {
      title: 'Today',
      color: colors.today,
      start: new Date(),
      allDay: true
    }
  ];
  constructor(
      public api: TestsDashboardService,
      public dialog: MatDialog,
      public api2: MyTestsService,
      public loading: LoadingService ) {}
  ngOnInit() {
    this.loading.loading(true);
    Promise.all([this.initDashboardView(), this.initScoreComparison(), this.initupcomingTests()]).then(() => {
      this.loading.loading(false);
      this.chartJudge = true;
    });
  }
  // 初始化upcoming模块
  initupcomingTests() {
    return new Promise((resolve) => {
      this.api2.api_get_UpcomingTests().then((res) => {
        this.upcomingTestsData = res;
        resolve();
      });
    });
  }
  // 改变注册状态
  changeRegistedStatus(ele) {
    const obj = {
      testID: ele.TestID,
      location: ele.Location,
      regDateString: ele.RegistrationDateAsString ,
      lateRegDateString: ele.LateRegistrationDateAsString,
      isRegistered: true ,
      subjectIDs: ele.Subjects ? ele.Subjects.split(',').map(Number) : [],
      RegDateIsModifiable: ele.RegDateIsModifiable
    }
    // 待确定
    this.api.api_post_SaveTest(obj).then((res) => {
      console.log(res, '修改成功');
    });
  }
  // 查看详情
  openViewDetails(val, ac) {
    let subjectsId = []
    let subjectsOptions = [];
    if (val.Subjects !== null) {
        subjectsId = val.Subjects.split(',').map(Number);
    }
    this.loading.loading(true);
    this.api.api_get_TestSubjects(val.TestID).then((res) => {
      subjectsOptions = res;
      let dialogRef =  this.dialog.open(ViewDetailsDialogMytestComponent,
          {
            height: '600px',
            width: '520px',
            data: {
              data: {
                TestType: val.TestType,
                TestDate: val.TestDateAsString,
                SubjectIds: subjectsId,
                Location: val.Location,
                RegistrationDate: new Date(val.RegistrationDateAsString),
                LateRegistrationDate: new Date(val.LateRegistrationDateAsString),
                isRegistered:  val.IsRegistered,
                regUrl: val.TestRegURL,
                TestId: val.TestID,
                RegDateIsModifiable: val.RegDateIsModifiable,
                TestInfoURL: val.TestInfoURL,
                subjectsOptions: subjectsOptions
              }
            }});
      dialogRef.afterClosed().subscribe(result => {
        // console.log(result, 'result');
        if (result) {
          console.log(result, 'result')
          if (result.subjectIDs.length > 0) {
            let subjectNames = [];
            result.subjectIDs.forEach((item) => {
              subjectsOptions.forEach((item2) => {
                if (item === item2.ID) {
                  subjectNames.push(item2.Name);
                }
              });
            });
            val.SubjectNames = subjectNames.join(',');
          }
          val.IsRegistered = result.isRegistered;
          val.LateRegistrationDateAsString = result.lateRegDateString;
          val.RegistrationDateAsString = result.regDateString;
          val.Subjects = result.subjectIDs.length > 0 ? (result.subjectIDs.join(',')) : '';
          val.Location = result.location;
          ac.renderRows();
          // this.initupcomingTests();

        }
      });
      this.loading.loading(false);
    });
  }
  // 删除行
  removeRow(val, i, up) {
    this.upcomingTestsData.splice(i, 1);
    up.renderRows();
    this.api.api_delete_DeleteTests(val.TestID);
  }
  // highcharts图
  initPie() {
    this.chartSAT = new Chart(<any>{
      chart: {
        type: 'solidgauge',
        margin: [0, 0, 0, 0]
      },
      title: null,
      pane: {
        center: ['50%', '85%'],
        size: '150%',
        startAngle: -90,
        endAngle: 90,
        background: {
          backgroundColor: '#a2d0f8',
          innerRadius: '100%',
          outerRadius: '80%',
          shape: 'arc'
        }
      },
      tooltip: {
        enabled: false
      },
      exporting: {
        enabled: false
      },
      yAxis: {
        min: 0,
        max: Number(this.satData) + 100,
        tickAmount: 6, // 刻度總數
        minorTickInterval: null,
        stops: [
          [0.1, '#1689ee']
        ],
        lineWidth: 0,
        tickWidth: 0,
        labels: {
          y: 8,
          rotation: 'auto',
        }
      },
      series: [{
        data: [this.satData],
        innerRadius: 80,
        dataLabels: {
          format: '<div style="text-align:center"><span style="font-size:16px;color:#1689ee">SAT</span><br/><span>{y}</span> </div>'
        }
      }],
      credits: {
        enabled: false
      },
      plotOptions: {
        solidgauge: {
          dataLabels: {
            y: 5,
            borderWidth: 0,
            useHTML: true
          }
        }
      }
    });
    this.chartACT = new Chart(<any>{
      chart: {
        type: 'solidgauge',
        margin: [0, 0, 0, 0]
      },
      exporting: {
        enabled: false
      },
      title: null,
      pane: {
        center: ['50%', '85%'],
        size: '150%',
        startAngle: -90,
        endAngle: 90,
        background: {
          backgroundColor: '#a2d0f8',
          innerRadius: '100%',
          outerRadius: '80%',
          shape: 'arc'
        }
      },
      tooltip: {
        enabled: false
      },
      yAxis: {
        min: 0,
        max: Number(this.actData) + 100,
        tickAmount: 6, // 刻度總數
        minorTickInterval: null,
        stops: [
          [0.1, '#1689ee']
        ],
        lineWidth: 0,
        tickWidth: 0,
        labels: {
          y: 8,
          rotation: 'auto',
        }
      },
      series: [{
        data: [this.actData],
        innerRadius: 80,
        dataLabels: {
          format: '<div style="text-align:center"><span style="font-size:16px;color:#1689ee">ACT</span><br/><span>{y}</span> </div>'
        }
      }],
      plotOptions: {
        solidgauge: {
          dataLabels: {
            y: 5,
            borderWidth: 0,
            useHTML: true
          }
        }
      },
      credits: {
        enabled: false
      }
    });
  }
  // 初始化ScoreComparison
  initScoreComparison() {
    return new Promise((resolve) => {
      this.api.api_get_ScoreComparison().then((res) => {
        this.bestScoresList = res.BestScores;
        resolve();
      });
    });
  }
  // 初始化 dashboardView
  initDashboardView() {
    // get ScoreComparison
    const self = this;
    return new Promise((resolve) => {
      this.api.api_get_DashboardView().then((res) => {
        // highcharts图数据
        this.satData = res.SATSS;
        this.actData = res.ACTSS;
        this.initPie();
        // 处理日历数据
        res.UpcomingTestItems.forEach((item) => {
          if (item.CalendarTestDate) {
            self.calendarEventArr.push({
              title: 'Test Date',
              color: colors.testDate,
              start: new Date(item.CalendarTestDate),
              allDay: true
            });
          }
          if (item.CalendarRegDate) {
            self.calendarEventArr.push({
              title: 'Reg Date',
              color: colors.regDate,
              start: new Date(item.CalendarRegDate),
              allDay: true
            });
          }
          if (item.CalendarLateRegDate) {
            self.calendarEventArr.push({
              title: 'Late Reg Date',
              color: colors.lateRegDate,
              start: new Date(item.CalendarLateRegDate),
              allDay: true
            });
          }
        });
        // 日历初始化
        self.events = this.calendarEventArr;
        resolve();
      });
    });

  }
}
