import { Component, OnInit, Inject, Renderer2 } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';

@Component({
  selector: 'app-edit-sat-dialog',
  templateUrl: './edit-sat-dialog.component.html',
  styleUrls: ['./edit-sat-dialog.component.css']
})
export class EditSatDialogComponent implements OnInit {
  satData: {
    [key: string]: any
  };
  satEssayReg: any = /^([0]|[2-8])$/;
  satMath2Reg: any = /^([0]|[1-3][0-9]|[1-3][0-9]([\\.][5])?|[4][0])$/;
  satOtherReg: any = /^([0]|[1-3][0-9]|[4][0])$/;
  constructor(@Inject(MAT_DIALOG_DATA) public data: any,
              public dialogRef: MatDialogRef<EditSatDialogComponent>,
              private renderer: Renderer2) { }
  cancle() {
    this.dialogRef.close();
  }
  save() {
    if (this.satData.type === 1) {
      if (this.satEssayReg.test(this.satData.data.ReadingEssay) && this.satEssayReg.test(this.satData.data.AnalysisEssay) && this.satEssayReg.test(this.satData.data.WritingEssay)) {
        this.satData.data.EssayTotal = Number(this.satData.data.ReadingEssay) +
            Number(this.satData.data.AnalysisEssay) +
            Number(this.satData.data.WritingEssay);
        this.dialogRef.close(this.satData.data);
      }
    }
    if (this.satData.type === 0) {
      if (this.satMath2Reg.test(this.satData.data.Math2) &&
          this.satOtherReg.test(this.satData.data.Reading2) &&
          this.satOtherReg.test(this.satData.data.WritingLanguage2) &&
          this.satOtherReg.test(this.satData.data.HistorySocialStudies2) &&
          this.satOtherReg.test(this.satData.data.Science2)) {
        this.satData.data.SubScores = Number(this.satData.data.Reading2) +
            Number(this.satData.data.WritingLanguage2) +
            Number(this.satData.data.Math2) +
            Number(this.satData.data.HistorySocialStudies2) +
            Number(this.satData.data.Science2);
        this.dialogRef.close(this.satData.data);
      }
    }
  }
  deepClone(obj) {
    let _obj = JSON.stringify(obj);
    let objClone = JSON.parse(_obj);
    return objClone;
  }
  onBlur(val, ac, simple) {
    let reg: any;
    // Essay
    if (simple === 0) {
      reg = this.satEssayReg;
    }
    if (simple === 1) {
      reg = this.satOtherReg;
    }
    if (simple === 2) {
      reg = this.satMath2Reg;
    }
    if (!reg.test(val)) {
      this.renderer.setStyle(ac, 'display', 'block');
    } else {
      this.renderer.setStyle(ac, 'display', 'none');
    }
  }
  ngOnInit() {
    console.log(this.data, 'data-------')
    this.satData = this.deepClone(this.data);
  }

}
