import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { SharedModule } from '../shared/shared.module';

import { TestingComponent } from './testing.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { MyTestComponent } from './my-test/my-test.component';
import { MyScoreComponent } from './my-score/my-score.component';
import { MyScoreComparisionComponent } from './my-score-comparision/my-score-comparision.component';
import { ScheduleTestComponent } from './schedule-test/schedule-test.component';
import { ViewDetailsDialogComponent } from './view-details-dialog/view-details-dialog.component';
import { ViewDetailsDialogMytestComponent } from './view-details-dialog-mytest/view-details-dialog-mytest.component';
import { EditSatDialogComponent } from './edit-sat-dialog/edit-sat-dialog.component';

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild([
      {
        path: '',
        component: TestingComponent,
        children: [
          { path: '',component: DashboardComponent},
          { path: 'Dashboard',component: DashboardComponent},
          { path: 'MyTest',component: MyTestComponent},
          { path: 'MyScore',component: MyScoreComponent},
          { path: 'MyScoreComparision',component: MyScoreComparisionComponent},
          { path: 'ScheduleTest',component: ScheduleTestComponent},
          { path: 'dia', component: EditSatDialogComponent }
        ]
      }
    ]
    )
  ],
  declarations: [
    TestingComponent,
    DashboardComponent,
    MyTestComponent,
    MyScoreComponent,
    MyScoreComparisionComponent,
    ScheduleTestComponent,
    ViewDetailsDialogComponent,
    ViewDetailsDialogMytestComponent,
    EditSatDialogComponent
  ],
  entryComponents: [
    ViewDetailsDialogComponent,
    ViewDetailsDialogMytestComponent,
    // EditSatDialogComponent
  ],
})
export class TestingModule { }
