import { Component, OnInit } from '@angular/core';
import { TestsDashboardService } from '../../service/service/testing/tests-dashboard.service';
import { LoadingService } from './../../shared/loading/service/loading.service';
@Component({
  selector: 'app-my-score-comparision',
  templateUrl: './my-score-comparision.component.html',
  styleUrls: ['./my-score-comparision.component.css']
})
export class MyScoreComparisionComponent implements OnInit {
  bestScoresList: any; // Best Individual Test
  scoreDetailListDataColumns: string[] = ['IsStar', 'TestDate', 'TestTypeName', 'TestScore'];
  scoreDetailListData: any = null;
  actSatListDataColumns: string[] = ['ACTComposite', 'SAT2015Total'];
  constructor( public api: TestsDashboardService, public loading: LoadingService ) { }

  ngOnInit() {
    this.loading.loading(true);
    Promise.all([this.init()]).then(() => {
      this.loading.loading(false);
    });
  }
  init() {
    return new Promise((resolve) => {
      this.api.api_get_ScoreComparison().then((res) => {
        this.bestScoresList = res.BestScores;
        this.scoreDetailListData = res.ACTSATTests;
        resolve();
      });
    });
  }
}
