import { Component, OnInit, ViewChild, TemplateRef, ViewContainerRef } from '@angular/core';
import { MatDialog, MatButton } from '@angular/material';
import { ViewDetailsDialogComponent } from '../view-details-dialog/view-details-dialog.component';
import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { TemplatePortal } from "@angular/cdk/portal";
import { TestScheduleService } from '../../service/service/testing/test-schedule.service';
import { LoadingService } from './../../shared/loading/service/loading.service';

@Component({
  selector: 'app-schedule-test',
  templateUrl: './schedule-test.component.html',
  styleUrls: ['./schedule-test.component.css']
})
export class ScheduleTestComponent implements OnInit {

  constructor(public dialog: MatDialog,
    private overlay: Overlay,
    private viewContainerRef: ViewContainerRef,
    public api: TestScheduleService,
    public loading: LoadingService) { }

  @ViewChild('addTestManually') addTestManually: TemplateRef<any>;
  @ViewChild('addTestBtn') addTestBtn: MatButton;
  tabsData = ['Sort by Test', 'Sort by Date'];
  selected: number = 0;
  overlayRef: OverlayRef;

  //类型列表
  typeList: any = [];
  //当前选中类型列表
  checkTypeItem: any = {};
  //测试日期
  testDateCode: any = new Date();
  //规则日期
  regularDateCode: any = new Date();
  //是否筛选过期时间
  pastDateFlag: boolean = true;


  //数据表头
  sortDataHeader: string[] = ['scheduled', 'registered', 'testdate', 'regular', 'late', 'subjects', 'ponit'];
  //Data数据列表
  sortDatas: any = [];

  ngOnInit() {
    this.initApiData();
    this.initPopupLayer();
  }

  //初始化添加弹出层
  initPopupLayer() {
    const addMenu = this.overlay.position().connectedTo(this.addTestBtn._elementRef,
      { originX: 'end', originY: 'bottom' },
      { overlayX: 'end', overlayY: 'top' });
    this.overlayRef = this.overlay.create({
      hasBackdrop: true,
      backdropClass: 'cdk-overlay-transparent-backdrop',
      positionStrategy: addMenu
    })
    this.overlayRef.backdropClick().subscribe(() => {
      this.overlayRef.detach();
    });
  }

  displayMenu() {
    if (this.overlayRef && this.overlayRef.hasAttached()) {
      this.overlayRef.detach();
    } else {
      this.overlayRef.attach(new TemplatePortal(this.addTestManually, this.viewContainerRef));
    }
  }

  //初始化接口数据
  initApiData() {
    this.loading.loading(true);
    Promise.all([this.getSortData(2),
    this.getTestManually()]).then(() => {
      this.loading.loading(false);
    });
  }

  /**
   * 获取数据列表
   * @param type 1 => by Data，2 => by test
   * @param isold 0 => 过期的不展示，1 => 全部数据
   */
  getSortData(type) {
    return new Promise((resolve) => {
      this.api.POST(this.api.configPath.TestDatesRead, {
        type: type,
        isold: this.pastDateFlag ? 1 : 0
      }).then((res) => {
        this.sortDatas = res;
        console.log(res, type);
        resolve();
      })
    })
  }

  //获取 Test Manually Select
  getTestManually() {
    return new Promise((resolve) => {
      this.api.GET(this.api.configPath.GetTestTypes, {}).then((res) => {
        this.typeList = res;
        this.checkTypeItem = this.typeList[0];
        // console.log('testManually', res);
        resolve();
      });
    });
  }

  //add manually
  addManually() {
    var time1 = new Date(this.testDateCode).getTime();
    var time2 = new Date(this.regularDateCode).getTime();
    if (time1 < time2) {
      alert('The registration date must be prior to the test date.');
      return;
    }
    this.displayMenu();
    let params = {
      testTypeID: this.checkTypeItem.ID,
      testDateString: this.testDateCode,
      regDateString: this.regularDateCode,
      location: "",
      subjectIDs: []
    };
    this.api.POST(this.api.configPath.CreateTest, params, true).then((res) => {
      this.toggleTab(0);
    });
  }

  //修改单条信息
  /**
   * 
   * @param obj testID: 0,
      location: "",
      regDateString: "",
      regDateIsModifiable: true,
      lateRegDateString: "",
      isRegistered: true,
      subjectIDs: [0]
   */
  saveTestInfo(obj) {
    this.api.POST(this.api.configPath.SaveTest, obj, true).then((res) => {
      this.toggleTab(this.selected);
    });
  }

  //获取下拉框subjects
  getSubjectList(id) {
    return new Promise((resolve) => {
      this.api.GET(this.api.configPath.GetTestSubjects, {
        testID: id
      }, true).then((res) => {
        // console.log(res, 'subjectList');
        resolve(res);
      });
    });
  }

  //切换tab
  toggleTab(event) {
    this.loading.loading(true);
    this.selected = event;
    if (event) {
      this.getSortData(1).then(res => {
        this.loading.loading(false);
      });
      return;
    }
    this.getSortData(2).then(res => {
      this.loading.loading(false);
    });
  }

  //查看详情 && 修改信息
  openViewDetails(val, type, index) {
    this.getSubjectList(val.TestTypeID).then(res => {
      const dialogRef = this.dialog.open(ViewDetailsDialogComponent, {
        width: '520px',
        height: '600px',
        data: {
          test: val,
          subjects: res
        }
      });
      dialogRef.afterClosed().subscribe(result => {
        if (!result) return;
        this.sortDatas[type].TestItems.splice(index, 1, result);
        // console.log(result);

      });
    });
  }


}
