import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class TestingFactoryService {
  public options: Object;
  // public apiUrl:string = 'https://pmc.mycca.net/api/GP2'
  public apiUrl:string = 'http://192.168.1.245:8009/api/GP2'
  // public apiUrl: string = '/api/GP2';
  protected headers:HttpHeaders =null;
  constructor(
      protected http: HttpClient,
      // protected headers:HttpHeaders=null,
  ) {
    if(this.headers==null){
      this.headers = new HttpHeaders({
        /*'Content-Type': 'application/x-www-form-urlencoded'*/
        /*'Content-Type': 'application/json; charset=utf-8'*/
        // application/json-patch+json
        'Content-Type': 'application/json-patch+json; charset=utf-8'

      });
    }

    this.options = { headers: this.headers, responseType: 'json' };
  }

  public get(query: string): Promise<any> {
    return new Promise<any>(resolve => {
      this.http.get(this.apiUrl + '' + query).subscribe(data => {
        resolve(data);
      }, error => {
        console.log(error);
        resolve(null);
      });
    });
  }

  protected put(query: string, posts: any): Promise<any> {
    return new Promise<any>(resolve => {
      this.http.put(this.apiUrl + query, JSON.stringify(posts), this.options).subscribe(data => {

        resolve(data);
      }, error => {
        console.log(error);
        resolve(null);
      });
    });
  }
  protected post(query: string, posts: any = null, options = null): Promise<any> {

    return new Promise<any>(resolve => {
      this.http.post(this.apiUrl + query, posts, this.options).subscribe(data => {
        resolve(data);
      });
    });
   /* return new Promise<any>(resolve => {
      console.log(posts, 'posts', JSON.stringify(posts));
      this.http.post(this.apiUrl + query + '?testTypeID=15&testDateString=2019%2F12%2F3&regDateString=2019%2F12%2F4&location=\'\'&subjectIDs=1&subjectIDs=2&subjectIDs=3', this.options).subscribe(data => {
        resolve(data);
      });
    });*/
    /*return new Promise<any>(resolve => {
     console.log(posts, 'posts', JSON.stringify(posts));
     this.http.post(this.apiUrl + query, {
       url: this.apiUrl + query,
       method: 'post',
       headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' },
       params: posts
     }, this.options).subscribe(data => {
       resolve(data);
     });
   });*/
    // {
    //     url: '/admin/api/login',
    //     method: 'post',
    //     headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' },
    //     params: params
    //   }
  }
}
