import { Injectable } from '@angular/core';
import { TestingFactoryService } from '../testing-factory.service';
import { HttpClient } from '@angular/common/http';
import { LoadingService } from './../../../shared/loading/service/loading.service';

@Injectable({
	providedIn: 'root'
})
export class TestScheduleService extends TestingFactoryService {
	public configUrl: string = "http://gp2.joyvio9.com/api/GP2";
	// public configUrl: string = "/api/GP2";
	public configPath: any = {
		//testSchedule 模块
		TestDatesRead: "/testing/TestDatesRead",
		GetTestTypes: "/testing/GetTestTypes",
		CreateTest: "/testing/CreateTest",
		GetTestSubjects: "/testing/GetTestSubjects",
		SaveTest: "/testing/SaveTest"
	};

	public GET(path: string, params: any = {}, isloading: boolean = false) {
		if (isloading) this.loading.loading(true);
		let and: string = '';
		let paramsString: string = '?';
		if (JSON.stringify(params) == "{}") paramsString = '';
		for (let key in params) {
			paramsString += and + key + '=' + params[key];
			and = '&';
		}
		return new Promise<any>(resolve => {
			this.http.get(this.configUrl + '' + path + paramsString).subscribe(data => {
				if (isloading) this.loading.loading(false);
				resolve(data);
			}, error => {
				if (isloading) this.loading.loading(false);
				console.log(error);
				resolve(null);
			});
		});
	}

	public POST(path: string, params: any = {}, isloading: boolean = false) {
		if (isloading) this.loading.loading(true);
		return new Promise<any>(resolve => {
			this.http.post(this.configUrl + path, params).subscribe(data => {
				if (isloading) this.loading.loading(false);
				resolve(data);
			}, error => {
				if (isloading) this.loading.loading(false);
				console.log(error);
				resolve(null);
			});
		});
	}

	// textSchedule页面
	// public api_get_TestDatesModel(id) {
	//   // id: 1:order by Date;2:order by Type
	//   return new Promise<any>(resolve => {
	//     this.get('/testing/GetTestDatesModel?id=' + id ).then(res => {
	//       resolve(res);
	//     });
	//   });
	// }

	constructor(protected http: HttpClient,
		protected loading: LoadingService) {
		super(http);
	}
}
