import { Component, OnInit ,Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import * as moment from 'moment';
import { TestsDashboardService } from '../../service/service/testing/tests-dashboard.service';
import { LoadingService } from '../../shared/loading/service/loading.service';
export interface testObj {
  TestType: string;
  TestDate: string;
  SubjectIds: any;
  Location: string;
  RegistrationDate: any;
  LateRegistrationDate: any;
  isRegistered:  any;
  regUrl: string;
  TestId: string;
  RegDateIsModifiable: any;
  TestInfoURL: '';
}

@Component({
  selector: 'app-view-details-dialog-mytest',
  templateUrl: './view-details-dialog-mytest.component.html',
  styleUrls: ['./view-details-dialog-mytest.component.css']
})
export class ViewDetailsDialogMytestComponent implements OnInit {

  constructor(@Inject(MAT_DIALOG_DATA) public data: any,
              public api: TestsDashboardService,
              public dialogRef: MatDialogRef<ViewDetailsDialogMytestComponent>,
              public loading: LoadingService) { }
  testDate: any;
  testObj: testObj = {
    TestType: '',
    TestDate: '',
    SubjectIds: [],
    Location: '',
    RegistrationDate: new Date('2019/4/12'),
    LateRegistrationDate: new Date('2019/4/12'),
    isRegistered:  true,
    regUrl: '',
    TestId: '',
    RegDateIsModifiable: false,
    TestInfoURL: ''
  };
  subjectsOptions: object[] = [];
  ngOnInit() {
    /**
     * 父级传参格式如下
     * TestType: '',
       TestDate: '',
       SubjectIds: [1, 2, 3],
       Location: '',
       RegistrationDate: new Date('2019/09/02'),
       LateRegistrationDate: new Date('2019/09/02'),
       isRegistered:  false,
       regUrl: '',
       TestId: 2344,
       RegDateIsModifiable: false,
       TestInfoURL: ''
     */
    this.testObj = this.data.data;
    this.subjectsOptions = this.data.data.subjectsOptions;
    // 获取subjects下拉框
    /*this.loading.loading(true);
    this.api.api_get_TestSubjects(this.testObj.TestId).then((res) => {
      this.subjectsLists = res;
      this.loading.loading(false);
    });*/

  }
  // 取消
  cancle() {
    this.dialogRef.close();
  }
  // 比较两个时间的大小
  checkTime(date1, date2) {
    date1 = date1.replace(/-/g, "/");
    date2 = date2.replace(/-/g, "/");
    var time1 = new Date(date1).getTime();
    var time2 = new Date(date2).getTime();
    if (time1 > time2) {
      return 1;
    } else if (time1 == time2) {
      return 2;
    } else {
      return 3;
    }
  }
  // 保存
  save() {
    // 如果父级传了参数subAlert则使用， 不传默认3个
    if (this.data.subAlert) {
      if (this.testObj.SubjectIds.length > this.data.subAlert.len) {
        alert('Only ' + this.data.subAlert.len + ' subjects may be entered for this type of test!');
        return;
      }
    } else {
      if (this.testObj.SubjectIds.length > 3) {
        alert('Only 3 subjects may be entered for this type of test!');
        return;
      }
    }
    const obj = {
      testID: this.testObj.TestId,
      location: this.testObj.Location,
      regDateString: moment(this.testObj.RegistrationDate).format('M/D/YYYY').indexOf('/') < 0 ? '' : moment(this.testObj.RegistrationDate).format('M/D/YYYY'),
      lateRegDateString: moment(this.testObj.LateRegistrationDate).format('M/D/YYYY').indexOf('/') < 0 ? '' : moment(this.testObj.LateRegistrationDate).format('M/D/YYYY') ,
      isRegistered: this.testObj.isRegistered,
      subjectIDs: this.testObj.SubjectIds,
      RegDateIsModifiable: this.testObj.RegDateIsModifiable
    }
    // regDateString必须在TestDate之前， lateRegDateString必须在TestDate之后
    if (this.checkTime(this.testObj.TestDate, obj.regDateString) === 3) {
      alert('The registration date must be prior to the test date');
      return;
    }
    if (this.checkTime(this.testObj.TestDate, obj.lateRegDateString) === 1) {
      alert('Late registration date must be after the registration date.');
      return;
    }
    // 待确定
    this.api.api_post_SaveTest(obj).then((res) => {
      /*if (res.errorMessage !== '') {
        alert(res.errorMessage);
      } else {
        this.dialogRef.close(obj);
      }*/
      // console.log(res, '修改成功');
    });
    this.dialogRef.close(obj);
  }

}
